  :root {
    /* Ujednolicone zmienne z prototypu tingtong.txt */
    --app-height: 100vh;
    --main-ui-bg-color: #696969;
    --transition-speed: 0.4s;
    --transition-timing: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    --topbar-base-height: 40px;
    --bottombar-base-height: 110px; /* Zwiększono wysokość bazową, aby pomieścić ikonki */
    --sidebar-width: 90px;
    --safe-area-top: env(safe-area-inset-top);
    --safe-area-bottom: env(safe-area-inset-bottom);
    --topbar-height: calc(var(--topbar-base-height) + var(--safe-area-top));
    --bottombar-height: calc(var(--bottombar-base-height) + var(--safe-area-bottom));

    /* Shadcn/ui variables - adapted for the prototype's theme */
    --background: 0 0% 0%; /* Black background */
    --foreground: 0 0% 100%; /* White text */

    --card: 222.2 84% 4.9%;
    --card-foreground: 210 40% 98%;

    --popover: 222.2 84% 4.9%;
    --popover-foreground: 210 40% 98%;

    --primary: 336 100% 50%; /* Accent color */
    --primary-foreground: 210 40% 98%;

    --secondary: 217.2 32.6% 17.5%;
    --secondary-foreground: 210 40% 98%;

    --muted: 217.2 32.6% 17.5%;
    --muted-foreground: 215 20.2% 65.1%;

    --accent: 336 100% 50%; /* Accent color */
    --accent-foreground: 210 40% 98%;

    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 210 40% 98%;

    --border: 217.2 32.6% 17.5%;
    --input: 217.2 32.6% 17.5%;
    --ring: 336 100% 50%;

    --radius: 0.5rem;
  }
  * {
  border-color: hsl(var(--border));
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
  html, body {
    height: 100%;
    width: 100%;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    color: hsl(var(--foreground));
    overscroll-behavior-y: contain; /* Prevent bounce scroll */
  }

  /*
    Logic for Body Background:
    - Default (Mobile): Black (#000000)
    - Desktop (md:): Zinc-950 to blend with emulator frame
  */
  body {
    background-color: #000000;
  }

  @media (min-width: 768px) {
    body {
      background-color: #09090b; /* zinc-950 */
